	module.exports = {"data":{"page":{"id":"/Users/MacbookAir/Desktop/myblog/content/pages/about/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/about-4fb642397fefe6c991dfa759d024fb7e-70d67.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"profile\"\n        title=\"\"\n        src=\"/static/about-4fb642397fefe6c991dfa759d024fb7e-70d67.png\"\n        srcset=\"/static/about-4fb642397fefe6c991dfa759d024fb7e-262d6.png 300w,\n/static/about-4fb642397fefe6c991dfa759d024fb7e-2ff47.png 600w,\n/static/about-4fb642397fefe6c991dfa759d024fb7e-70d67.png 1200w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2>軽い自己紹介</h2>\n<p>北海道出身の22歳大学4年生。スノーボードと犬が好き。</p>\n<p>大学3年次の後期に東南アジア、インド、中国を放浪しつつソフトウェアエンジニアとしてフリーランスを経験し、ソーシャルシェアリングサービス系の開発やウェブメディアの運営・バイアウト、SEOコンサルティングを経験しました。並行して大学の講義をリモートで受講していたので、3年次を修了しました。</p>\n<p>その後、1年間休学しサンフランシスコ・シリコンバレーを拠点に<a href=\"http://btrax.com/jp/\" target=\"_blank\" rel=\"noopener noreferrer\">btrax</a>でソフトウェアエンジニアインターンとして働き始めました。現在は休学2年目になり、継続してお仕事をしています。</p>\n<p>btraxでは、マーケティング50%（主にSEO）、デベロップメント50%くらいのタスクです。SEOに関してはバチクソ詳しいです。</p>\n<h2>僕が提供できるコンテンツの価値</h2>\n<p>僕自身は、大学生からプログラミングの独学を始め、英語に至ってはサンフランシスコでインターンをする半年前に中学レベルの参考書から勉強をし直したという急激なキャリアチェンジを経験しているので、「今からじゃ遅いかな？」と考えている人に有益な情報を提供できます。</p>\n<p>また、もともとビジネス・テクノロジーに興味があり、現在はサンフランシスコ・シリコンバレー地域のベイエリアで1年以上働いているので、スタートアップ情報や、投資情報、最新テクノロジーに詳しいです。</p>\n<p>このブログやSNSでは、上記の情報だけでなく、海外で働きたい人、英語やプログラミングの初学者に向けて有益な情報を発信しています。SNSではブログで書かないこともつぶやいてるので興味あるかたはフォローしてください！今後の活動の励みになります。</p>\n<div class=\"box\">\n<ul>\n<li>\n<p><a href=\"https://twitter.com/masakinishi_com/\" target=\"_blank\" rel=\"noopener noreferrer\">Masaki Nishi＠エンジニア (@masakinishi_com) | Twitter</a></p>\n</li>\n<li>\n<p><a href=\"https://www.facebook.com/masakinishicom/\" target=\"_blank\" rel=\"noopener noreferrer\">Facebook</a></p>\n</li>\n<li>\n<p><a href=\"https://www.instagram.com/masakinishi_com/\" target=\"_blank\" rel=\"noopener noreferrer\">Instagram</a></p>\n</li>\n</ul>\n</div>\n<h3>過去のbtraxブログでの代表執筆記事</h3>\n<div class=\"box\">\n<ul>\n<li>\n<p><a href=\"http://blog.btrax.com/jp/2018/06/01/retail-store-startup/\" target=\"_blank\" rel=\"noopener noreferrer\">小売業界の敵はAmazonではない? これからの小売が知っておくべき課題</a> | 524 shares | <a href=\"https://newspicks.com/news/3072396\" target=\"_blank\" rel=\"noopener noreferrer\">811 picks</a></p>\n</li>\n<li>\n<p><a href=\"http://blog.btrax.com/jp/2018/05/09/bikeshare-in-sanfrancisco/\" target=\"_blank\" rel=\"noopener noreferrer\">シェアサイクル事業問題から見るサンフランシスコ市の意思決定の早さ</a> | 238 shares</p>\n</li>\n<li>\n<p><a href=\"http://blog.btrax.com/jp/2017/10/16/millennials-characteristics/\" target=\"_blank\" rel=\"noopener noreferrer\">95年生まれが思う日本とアメリカのミレニアル世代の特徴と消費行動</a> | 171 shares</p>\n</li>\n<li>\n<p><a href=\"http://blog.btrax.com/jp/2018/06/28/millennials-mindset-startup/\" target=\"_blank\" rel=\"noopener noreferrer\">ミレニアル世代のマインドセットを捉えて成功したスタートアップ事例</a> | 138 shares</p>\n</li>\n<li>\n<p><a href=\"http://blog.btrax.com/jp/2017/08/29/ai-history/\" target=\"_blank\" rel=\"noopener noreferrer\">人工知能(AI)のできることとは？歴史から学ぶ現状と未来予測</a> | 169 shares</p>\n</li>\n<li>\n<p><a href=\"http://blog.btrax.com/jp/2017/09/15/affected-by-blockchain/\" target=\"_blank\" rel=\"noopener noreferrer\">ブロックチェーン技術の仕組みが大きな影響を与える15の業界</a> | 401 shares</p>\n</li>\n</ul>\n</div>\n<h2>近い将来の予定</h2>\n<p>これからの予定ですが、2020年に日本の大学を卒業後、ドイツで新卒フリーランスとして移住し、ドイツのミュンヘンにある大学で修士号ではなく、第2学士号を取得する予定です。</p>\n<p>お仕事の依頼や質問は<a href=\"/contact/\">お問い合わせ</a>からお願いいたします。</p>","frontmatter":{"title":"プロフィール"}},"footnote":{"id":"/Users/MacbookAir/Desktop/myblog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>© 2018 <a href=\"/\">MasakiNishi.com</a></li>\n<li><a href=\"/user-terms/\">利用規約</a> | <a href=\"/privacy-policy/\">プライバシーポリシー</a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"..."}}}},"pathContext":{"slug":"/about/"}}

